import "../Blogs/blog.css";
import ReactHtmlParser from "react-html-parser";
import Footer from "../Footer/footer";
import BackButton from "../BackButton/backButton";
import Loader from "../Loader/loader";
import projectListJSON from "../helper/projectList.json";
export default class projectList extends React.Component {
  constructor(props) {
    super(props);
    this.state = { data: props.data };
    this.genList = this.genList.bind(this);
    this.fetchData = this.fetchData.bind(this);
  }
  getTime(timeinSec) {
    var utcSeconds = timeinSec;
    var d = new Date(0);
    d.setUTCSeconds(utcSeconds);
    return d.getFullYear();
  }
  constructCard(data, i) {
    return (
      <div
        key={"card" + i}
        onClick={() => {
          window.open(data.link, "_blank");
        }}
        className={i == 0 ? "card first" : "card"}
      >
        <h2>{ReactHtmlParser(data.title)}</h2>
        <p className="date">{this.getTime(data.timestamp["_seconds"])}</p>
        <p>{ReactHtmlParser(data.content)}</p>
      </div>
    );
  }
  genList() {
    let ls = [];
    for (let i = 0; i < this.state.data.length; i++) {
      ls.push(this.constructCard(this.state.data[i], i));
    }
    return ls;
  }
  async fetchData() {
    try {
      let data = await fetch("/api/projectsList");
      let response = await data.json();
      this.setState({ data: response["data"] });
    } catch (error) {
      this.setState({ data: projectListJSON["data"] });
    }
  }
  componentDidMount() {
    if (!this.state.data) {
      this.fetchData();
    }
  }

  render() {
    if (this.state.data) {
      return (
        <>
          <BackButton />
          <div className="wrapper">
            <div className="top"></div>
            <div className="content">{this.genList()}</div>
          </div>
          <Footer />
        </>
      );
    } else {
      return <Loader />;
    }
  }
}
